.App {
  /* text-align: center; */
}

.App-logo {
  /* animation: App-logo-spin infinite 10s linear; */
  height: 40px;
}

.App-header {
  background-color: #161515;
  /* height: 150px; */
  padding: 20px;
  color: white;
  text-align: left;
}

.App-title {
  font-size: 1.5em;
  display: inline-block;
}

.App-intro {
  font-size: large;
}

.App-footer {
  position: fixed;
  bottom: 0;
  height: 20px;
  padding: 20px;
  color: #605c5c;
  font-size: 1.1em;
}

.App-footer a {
  color: #605c5c;
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



